.App {
  margin: 30px auto;
  padding: 10px;
  width: 60vw;
  height: auto;
  border: 2px solid teal;
  border-radius: 8px;
  text-align: center;
  background: linear-gradient(0deg, rgba(34,193,195,1) 0%, rgba(253,187,45,1) 100%);
  word-wrap: break-word;
}

.app-title {
  text-shadow: 2px 2px 2px pink;
}

.todo-input {
  padding: 10px;
  font-size: 16px;
  margin: 10px;
  border-radius: 8px;
  height: 20px;
  color: #060885;
  border: 2px solid teal;

}

.todo-input:focus {
  outline: 4px dashed lightskyblue;
      outline-offset: 0.2em;
}

.button {
  padding: 9px 16px;
  font-size: 18px;
  background-color: #dbbb53;
  border-radius: 8px;
  cursor: pointer;

}

.button:hover {
  background-color: #a99221;
  color: #95efe0;
}


.list {
  margin: 5px 20px;
  padding: 0;
}

.list-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  text-align: left;
  gap: 8px;
  width: 50vw;
  padding: 8px 16px;
  margin: 2px;
  font-size: 18px;
  color:#05065c;
  border: 1px solid gray;
  border-radius: 16px;
  box-shadow: inset 0px 8px 10px 0px rgba(256,235,235,1);
  list-style-type: none;
}

.text-counter{
  font-size: 18px;
  font-weight: bold;
  color: teal;
  text-shadow: 1px 1px #c0d806;
}

.btn-del {
  padding: 6px 10px;
  font-size: 16px;
  color: #29064d;
  background-color: transparent;
  border-radius: 20px;
  border: none;
  cursor: pointer;
}

.btn-del:hover {
  background-color: #d36956;
  color: #ebf7eb;
}

.todo-list {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  gap: 8px;
}

.btn-clear {
  margin: 0;
  padding: 8px 20px;
  font-size: 16px;
  color: #29064d;
  background-color: transparent;
  border-radius: 8px;
  border: 1px solid teal;
  box-shadow: inset 0px 8px 10px 0px rgba(256,235,235,1);
  cursor: pointer;
  transition: all 0.2s;
}

.btn-clear:hover {
  background-color: #e12327;
  color: #a4ddde;
  transform: scale(1.02);
}